@import url(https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,400;1,800&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Mulish', sans-serif;
}
.inner{
  overflow: hidden;
}
.inner img{
  transition: all 1.5s ease;
}
.inner:hover img{
  transform: scale(1.2);
}

.navbar a {
  font-size: 1.8rem;
  text-transform: capitalize;
}
.welcome{
  position: relative;
  margin-top: 100px;
  margin-bottom: 22px;
  font-size: 50px;
  text-align: center;
}
.head1{
  color: yellowgreen;
  font-size: 70px;
  text-align: center;
}
.head2{
  font-size: 30px;
  text-align: center;
  margin-top: 30px;
}
.home_header_img img{
  display: block;
  width: 100%;
  border-radius: 50px;
}

#nav_bg {
  margin-top: 100px; 
}


/* select project */
.select_project{
  text-align: center;
  margin-top:100px ;
}
.card{
  box-sizing: border-box;
}
.card-body{
text-align: center;
font-size: 25px;
}
.nav-link{
  color: black;
}
.card-body a:hover{
   color: yellowgreen
}
.card-margin{
  margin: 50px;
}
.section{
  padding-top: 70px;
  padding-bottom: 70px;
}
.section-head h1{
  color: black;
  text-align: center;
  margin-bottom: 40px;
  font-size: 50px;
}
.section-head h1:after{
  background: #e74c3c;
  content: "";
  display: block;
  width: 100px;
  height: 3px;
  margin: 20px auto;
}
.section-head1 h1{
  color: white;
  text-align: center;
  margin-bottom: 40px;
}
.section-head1 h1:after{
  background: #e74c3c;
  content: "";
  display: block;
  width: 100px;
  height: 3px;
  margin: 20px auto;
}



/* testimonial */
#testimonial{
  color: rgb(255, 255, 255);
  background: url(/cloud/static/media/phone.6bd8d3b9.jpg) no-repeat 0;
  background-size: cover;
  padding: 50px 0;
  position: relative;
}
#testimonial:before{
  background: rgba(8, 8, 8, 0.4);
  content: "";
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
}
.testimoni{
  text-align: center;
  color: yellowgreen;
  border-bottom: 2px solid red;
  
}
.testimonial img{
  height: 120px;
  width: 130px;
  border-radius: 50%; 
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.text1{
  color: #e74c3c;
  font-size: 40px;
  text-align: center;
}
.textt{
  text-align: center;
}









